/* СТИЛІ ДЛЯ СКРОЛУ  */

.container {
  width: 100%;
  overflow-y: hidden;
  overflow-x: auto;
  padding-bottom: 24px;
}

@media screen and (min-width: 768px) {
  .container {
    padding-bottom: 52px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    padding-bottom: 16px;
  }
}

.container::-webkit-scrollbar {
  width: 5px;
  height: 10px;
}

.container::-webkit-scrollbar-track {
  background: var(--card-list-scroll-bar);
  border-radius: 8px;
  opacity: 0.08;
}

.container::-webkit-scrollbar-thumb {
  background: var(--card-list-scroll-thumb);
  border-radius: 5px;
}

.container::-webkit-scrollbar-thumb:hover {
  background: var(--theme-text-hover-color);
}
